<template>
    <div id="event-list">
        <div class="event" v-for="event in futureEvents" :key="event.title+event.startTime">
            <Event :info="event" v-if="event !== undefined"/>
        </div>
        <h2 v-if="pastEvents.length > 0">Past Events</h2>
        <div class="event" v-for="event in pastEvents" :key="event.title+event.startTime">
            <Event :info="event" v-if="event !== undefined"/>
        </div>
    </div>
</template>

<script>
import Event from './Event'

export default {
    name: "EventList",
    components: {
        Event
    },
    props: {
        events: Array
    },
    computed: {
        nonTodayEvents() {
            let currentDate = new Date();
            return this.events.filter(e => e.date.day != currentDate.getDate() || e.date.month != currentDate.getMonth()+1) 
        },
        futureEvents() {
            return this.nonTodayEvents.filter(e => e.endTime >= (new Date()).getTime())
        },
        pastEvents() {
            return this.nonTodayEvents.filter(e => e.endTime < (new Date()).getTime())
        }
    },
}
</script>

<style scoped>
h2  {
  display: block;
  color: var(--primary);
  font-size: 2.5rem;
  line-height: 1.2;
  font-weight: 600;
  text-align: center;
  text-transform: capitalize;

  margin-bottom: 30px;

  
}
</style>       